:root {
    --allcolor: rgba(255, 166, 0, 0.23);
    --sidebarcolor: orange;
}

body {
    margin: 0;
    padding: 0;
    background-color: var(--allcolor);
    /* opacity: 0.5; */
    font-family: 'Tai Heritage Pro', serif;
}

.heading {
    font-size: 3rem;
    text-align: center;
    text-decoration: underline;
}

ul {
    font-size: 1.5rem;
}


