.containers {
    display: flex;
    /* background-color: green; */
    /* border: 2px solid green; */
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
}

.slidebar nav ul {
    list-style: none;
    margin: 32px;
    /* text-align: center; */
    /* align-items: center; */
    /* display: flex; */
    padding: 0;
}

.slidebar nav {
    /* list-style: none; */
    margin-top: 50%;
    /* align-items: center; */
    /* justify-content: center; */
    /* display: flex; */
    /* padding: 0; */
}

.slidebar nav ul li {
    /* list-style: none; */
    /* margin: 0; */
    padding: 1vh 0;
}

.slidebar nav ul li a {
    text-decoration: none;
    color: white;
}

.slidebar nav ul li a:hover {
    /* text-decoration: none; */
    color: rgb(5, 226, 45);
}

.slidebar {
    background-color: var(--sidebarcolor);
    font-size: 2vw;
    min-width: 15vw;
    height: 100%;
    display: flex;
    justify-content: center;
    font-family: 'Oswald', sans-serif;
    /* align-items: center; */
}

.mainContant {
    /* background-color: greenyellow; */
    width: 100%;
    height: 100%;
    /* display: flex; */
    justify-content: center;
    align-items: center;
    overflow-y: scroll;
}


/* Introduction css start here */

.box2 {
    width: 100%;
    height: 50%;
    box-sizing: border-box;
    padding: 1rem;
    /* display: flex; */
    /* justify-content: space-around; */
    /* align-items: center; */
    /* background-color: blue; */
    /* border: 2px solid black; */
}

.box1 {
    width: 100%;
    min-height: 100%;
    padding: 1rem;
    background-color: rgb(252, 246, 237);
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box1:nth-child(even) {
    background-color: rgb(251, 240, 225);
    display: flex;
    justify-content: center;
}

.box1 h1 {
    font-size: 5vw;
    /* text-emphasis: initial; */
    /* text-transform: uppercase; */
    color: rgb(0, 0, 0);
    /* padding-left: 3rem; */
    margin: 0;
    text-align: center;
}

.box1 h3 {
    margin: 0;
    font-size: 1rem;
    /* text-emphasis: initial; */
    text-align: center;
    text-transform: uppercase;
    color: rgb(8, 8, 8);
    /* padding-left: 3rem; */
    /* text-align: left; */
}

.box1 p {
    /* margin: 0; */
    font-size: 1rem;
    /* text-emphasis: initial; */
    text-align: center;
    /* text-transform: uppercase; */
    /* color: rgb(8, 8, 8); */
    /* padding-left: 3rem; */
    /* text-align: left; */
}

.box1 span {
    color: rgb(172, 54, 54);
}

.btn {
    border: 2px solid black;
    padding: 9px 14px;
    border-radius: 22px;
    cursor: pointer;
    background-color: white;
    margin: 5px;
}

#profile_pic {
    border: 1vw solid rgb(203, 134, 6);
    background: url(./profile.jpg) 5px -15px;
    background-size: 9vw;
    background-repeat: no-repeat;
    background-color: rgb(40, 34, 34);
    border-radius: 50%;
    /* filter: invert(); */
    width: 10vw;
    height: 10vw;
}

.profile_pos {
    /* style={{width:"100%",display:"flex", justifyContent:"center", margin:"0"}} */
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 0;
}


/* Contact page css start here */

::placeholder {
    color: rgb(53, 120, 178);
    /* opacity: 0; */
    /* display: none;/ */
    /* visibility: hidden; */
}

.sec {
    border: 2px solid violet;
    border-radius: 10px;
    background-color: aquamarine;
    padding: 2%;
    width: 100%;
    margin: 10vw;
    text-align: center;
}

.rows {
    display: flex;
    background-color: aqua;
    /* width: 100%; */
    /* margin: 10px; */
    /* border: 2px solid black; */
    justify-content: space-evenly;
}

.forms {
    /* border: 2px solid black; */
    padding: 10px;
    width: 50%;
}

.forminput {
    padding: 5px 15px;
    /* border: 2px solid rgb(255, 255, 255); */
    background: none;
    border-radius: 20px;
    outline: none;
    /* border: none; */
    width: 100%;
}

form span {
    position: absolute;
    color: white;
    margin: 5px;
    /* border: none; */
    /* padding-left: px; */
    /* padding-right: 2px; */
    transform: translateX(20px) translateY(-18px);
    display: none;
}

form span::before {
    content: "";
    /* background-color: aqua; */
    width: 120%;
    height: 50%;
    z-index: -1;
    position: absolute;
    transform: translateX(-5px) translateY(10px);
    /* display: none; */
}

.formcontainer {
    /* border: 2px solid black; */
    margin: 10px;
}


/* footer style here */

.footer {
    /* margin: 10px; */
    /* padding-top: 30px; */
    position: relative;
    bottom: 0px;
    background-color: rgb(129, 36, 36);
    /* color: blac; */
}

.footer p {
    margin: 0;
    padding: 2vh;
    text-align: center;
    color: white;
    background-color: rgb(82, 79, 79);
}


